<template>
  <v-container>
    <v-row class="fill-height" align="center" justify="center">
      <v-col>
        <v-card min-width="150px">
          <v-card-title> Date range </v-card-title>
          <v-card-text>
            <v-date-picker
              v-model="daterange"
              range
              no-title
              color="#36213e"
              full-width
              @change="Filtersearch"
            ></v-date-picker>
          </v-card-text>
        </v-card>

        <v-row v-for="chart in charts" :key="chart">
          <v-col>
            <v-card min-width="150px">
              <v-card-title>
                {{ chart.title }}
              </v-card-title>
              <v-card-text>
                <v-sparkline
                  :value="chart.value"
                  :gradient="gradient"
                  :smooth="radius || false"
                  :padding="padding"
                  :line-width="width"
                  :stroke-linecap="lineCap"
                  :gradient-direction="gradientDirection"
                  :fill="fill"
                  :type="type"
                  :auto-line-width="autoLineWidth"
                  auto-draw
                ></v-sparkline>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Axios from 'axios'

const gradients = [["#9dbad7", "#3e1963"]];
export default {
  data() {
    return {
      width: 2,
      radius: 10,
      padding: 2,
      lineCap: "round",
      gradient: gradients[0],
      gradientDirection: "bottom",
      gradients,
      fill: false,
      type: "trend",
      autoLineWidth: false,
      daterange: [],

      charts: [
        {
          title: "Logins",
          value: [],
          activeDays: []
        },
        {
          title: "New Cards",
          value: [0, 2, 5, 9, 5, 10, 3, 5, 0, 0, 1, 8, 2, 9, 0],
        },
        {
          title: "Emails sent",
          value: [2, 3, 10, 30, 100, 100, 100, 100, 99],
        },
        {
          title: "Signups",
          value: [0, 2, 5, 9, 5, 10, 3, 5, 0, 0, 1, 8, 2, 9, 0],
        },
      ],
    };
  },
  methods: {
    Filtersearch(){
      this.charts[0].value = []
      this.charts[0].activeDays = []
      //Logins from activity table y-date x-count
      Axios.get("/getactivities").then((response) => {
        for(let i = 0; i < response.data.length; i++){
          if(response.data[i].date >= this.daterange[0] && response.data[i].date <= this.daterange[1]){
            this.charts[0].activeDays.push(response.data[i].date)
          }
        }
          let count = 0
        for(let x = 0; x < this.charts[0].activeDays.length-1; x++){
          if(this.charts[0].activeDays[x] = this.charts[0].activeDays[x+1]){
            count++
            x++
          }else{
            this.charts[0].value.push(count)
          }
        }
        console.log(this.charts[0].value)
      })
      //new cards from itemperson table y-date x-count
      //emails sent from my deals 
      //signups from person table (joined date)
    }
  }
};
</script>

<style>
</style>


